<template>
	<div id='miniPlay'>
		 <footer :style="{backgroundImage:'url('+newactiveMusic.logo+')',backgroundRepeat:'no-repeat',backgroundSize:'cover'}">
     	 <div class='progress-bar'><div class='bar-in'></div></div>
    	<div class='img'>
    	     	 <img  alt="" :src='newactiveMusic.logo'>
    	</div>
    	     	<div class='info'>
    	     	    	      	 <p>{{newactiveMusic.music}}</p>
      							 <p>{{newactiveMusic.artist}}</p>
    	     	    	</div>

                      <span class='pause iconfont icon-zanting1' @click='pause'></span>
                      <span class='play icon-play iconfont' @click='play'></span>
    	     	    	         <audio  autoplay="autoplay" :src="newactiveMusic.src"  id="musicPlay"></audio>
   		 </footer>
	</div>
</template>
<script>
import store from '@/vuex/store';
import {mapState} from 'vuex';
	export default {
		mounted:function() {
			for (let k in this.newmusicData[0]) {
				this.newactiveMusic[k] = this.newmusicData[0][k];
			}
      setInterval(function(){
      let music = document.getElementById('musicPlay');
      let rate = music.currentTime/music.duration;
     var bar = document.querySelector('.bar-in');
      bar.style.width = rate*100 +'%';
  },1000)
		},
    methods:{
      pause:function() {
        document.getElementById('musicPlay').pause(); 
      },
      play:function() {
        document.getElementById('musicPlay').play(); 
      }
    },
		computed:{
			...mapState(['newmusicData','newactiveMusic'])
		},
		store
	}
 
     
  
 

</script>
<style>
	footer {
  position: fixed;
  height: 72px;
  width: 100%;
  background:#ccc;
  bottom: 0;
}
footer .progress-bar {
  width: 100%;
  height: 2px;
  background: transparent;
}
.bar-in {
  width: 0%;
  height: 2px;
  background: #ff8500;
}
footer .img {
  padding: 6px;
  width: 60px;
  height: 60px;
  float: left;
}
footer .img img {
  width: 100%;
  height: 100%;
}
footer .info {
  float: left;
  padding: 10px;
  height: 60px;
  padding: 6px;
  font-size: 14px;
  text-align: left;
  color: #fff;
} 
footer .info p:nth-of-type(1) {
  font-size: 18px;
}
footer .play,.pause {
  float: right;
  font-size: 30px;
  color: #FF8500;
}
.play {
  margin: 15px 30px 0 0;
}
.pause {
  margin: 15px 20px 0 0;
}
</style>


